import { request } from 'utils';

const prefix = '/api/categories';

export const getListNation = () =>
  request({
    prefix,
    url: '/Nation',
    method: 'GET',
  });
export const getNation = id =>
  request({
    prefix,
    url: `/Nation/GetById`,
    method: 'GET',
    params: { id },
  });
export const deleteNation = id =>
  request({
    prefix,
    url: `/Nation`,
    method: 'DELETE',
    data: { id },
  });
export const updateNation = (id, values) =>
  request({
    prefix,
    url: `/Nation`,
    data: {
      id,
      label: values.name,
      labelInEnglish: '',
    },
    method: 'PUT',
  });
export const createNation = values =>
  request({
    prefix,
    url: '/Nation',
    data: {
      id: values.code,
      label: values.name,
      labelInEnglish: '',
    },
    method: 'POST',
  });



